import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import UOFM from './assets/uofm.png'
import GitHubIcon from '@mui/icons-material/GitHub';
import Link from '@mui/material/Link';
import { useTheme } from '@mui/material/styles';
import Grid from '@mui/material/Grid2';
export default function MediaCard({image, title, text, githubLink, otherLink}) {
    var githubLinkAction = ''
    var otherLinkAction = ''
    const theme = useTheme();
    if (githubLink !== undefined) {
        githubLinkAction = (
            <CardActions>
                <Link href={githubLink} color={theme.palette.primary.dark}>
                   <Button variant='contained' sx={{border:"2px solid black"}}>
                    <GitHubIcon/>
                   </Button>
                </Link>
               
            </CardActions>
        )
    }
    return (
        <Grid size={{ sm: 12, md: 4 }}>
            <Card sx={{ maxWidth: '100%' }}>
            <CardMedia
                sx={{ height: 200 }}
                image={image}
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="div" align='left'>
                {title}
                </Typography>
                <Typography variant="body2" sx={{ color: 'text.secondary' }} align='left'>
                {text}
                </Typography>
            </CardContent>
            <CardActions>
                {githubLinkAction}

            </CardActions>
            </Card>
        </Grid>
    );
}