import { Typography } from '@mui/material';
import { Box } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
import Fade from '@mui/material/Fade';
import React from 'react';
import profilePic from './assets/profilePic.jpeg'
import { TypeAnimation } from 'react-type-animation';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import EmailIcon from '@mui/icons-material/Email';
import Container from '@mui/material/Container'
import Link from '@mui/material/Link';
import Grow from '@mui/material/Grow';
import TitlebarImageList from './TechStack.js';
import Grid from '@mui/material/Grid2';
import ImageListItem from '@mui/material/ImageListItem';

import techStack from './assets/techStack.jpg'

export default function Home() {
    const theme = useTheme();
    return (
        <Box bgcolor={theme.palette.primary.dark} alignItems="center" justifyContent="center">
            
            <Stack spacing={2} margin="auto"  alignItems="center" justifyContent="center">
                <Grow in={true}>
                <Avatar alt="Daniel Tok" src={profilePic} sx={{ width: 150, height: 150, margin: 'auto' }} />
                </Grow>
                
                <Typography color={theme.palette.primary.light} variant='h3' align='center'>
                    <b>Hi! I'm DANIEL TOK.</b>
                </Typography>
                <TypeAnimation
                sequence={[
                
                    'Senior @ University of Michigan - Ann Arbor',
                    1000, 
                    'ITS Network Infrastructure Intern',
                    1000,
                    'B.S.E Computer Science',
                    1000,
                    'AWS Certified Developer Associate',
                    1000,
                ]}
                wrapper="span"
                speed={80}
                style={{ fontSize: '1.5em', display: 'inline-block',color:'white', margin:'auto'}}

                repeat={Infinity}
                />
        </Stack>
                <br/>
            <Stack direction={'row'}  spacing={2} alignItems="center"
  justifyContent="center">
                <Link href='https://www.linkedin.com/in/daniel-tok-um/' underline="none">
                    <LinkedInIcon sx={{color:'white'}} fontSize='large'/>
                </Link>
                <Link href='https://github.com/danielxt'>
                    <GitHubIcon sx={{color:'white'}} color='primary' fontSize='large'/>
                </Link>
                <Link href='#Contact'>
                    <EmailIcon sx={{color:'white'}} color='primary' fontSize='large'/>
                </Link>
               
               
            </Stack>
            <Typography color={theme.palette.primary.text}>
            <Container sx={{width:'75%'}}>
            <p>Hello! I'm a senior studying Computer Science at the University of Michigan - Ann Arbor.</p>

<p>My main interests are in building web applications and developing solutions utilizing cloud services, particularly with AWS.
</p>
<p>
I'm currently working on the UofM ITS Network Infrastructure Team where I develop data synchronization scripts for our Netbox fork, build CI/CD deployment pipelines, deploy containerized applications using Docker and Openshift, and build web applications for monitoring wifi data using React and FastAPI.

</p>
<p>In my free time, I enjoy playing tennis and following Formula 1.</p>

Let's connect via email or LinkedIn!
<span id='TechStack'></span>
<Typography variant='h3' color={theme.palette.primary.text} paddingBottom={2} paddingTop={2}>
                    <b>MY TECH STACK</b>
                    </Typography>
            </Container>
    
            </Typography>
        
            
            <Box sx={{display: 'flex', justifyContent: 'center', height: '300px'}}>
                
                  
            <img src={techStack} height='100%'/>
            </Box>
      
           
  
          
        
            
   
            
      
        </Box>
       
    )
}