import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { createTheme, ThemeProvider, styled } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';
import Container from '@mui/material/Container'
import { Typography } from '@mui/material';
import Button from '@mui/material/Button';
import { useState } from 'react';
export default function MultilineTextFields() {
    const [formData, setFormData] = useState({
        email: "",
        message: ""
      });
      const theme = useTheme();
    
      const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData((prevState) => ({
          ...prevState,
          [name]: value,
        }));
      };
    
      const handleSubmit = (e) => {
        e.preventDefault();
        console.log(formData);
        // Add form submission logic here
        fetch(`https://formsubmit.co/danielxt@umich.edu&formData=${formData}`, 
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ title: 'React POST Request Example' }),
                redirect: 'follow'
            }
          
        ) .then(response => {
            // HTTP 301 response
            // HOW CAN I FOLLOW THE HTTP REDIRECT RESPONSE?
            if (response.redirected) {
                window.location.href = response.url;
            }
        })
        .catch(function(err) {
            console.info(err + " url: ");
        });
      };
    
      return (
        <Box
          component="form"
          onSubmit={handleSubmit}
          sx={{ display: "flex", flexDirection: "column", gap: 2 }}
          margin={2}
          method='post'
          padding={2}
        >
                  <Typography variant='h3' color={theme.palette.primary.dark} paddingBottom={2}>
                        <b id="Contact">CONTACT ME </b>
                        </Typography>

                        <Typography variant='h6' color={theme.palette.primary.dark} paddingBottom={2}>
                        Leave me a message at danielxt@umich.edu
                        </Typography>
                        <form action="https://formsubmit.co/560c8135db7e7587837bfa282d426793" method="POST">
  
          <TextField
            label="Your email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            fullWidth
          >
            <input type="text" name="email" required/>
            </TextField>
          <TextField
            label="Message"
            name="message"
            value={formData.message}
            onChange={handleChange}
            rows={5}
            fullWidth
            multiline
          >
             <input type="email" name="message" required/>
            </TextField>
          <Button type="submit" variant="contained">
            Submit
          </Button>
          </form>
        </Box>
      );
}