import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Card from './Card.js'
import UOFM from './assets/uofm.png'
import chataway from './assets/chataway.png'
import kpmg from './assets/kpmg.png'
import roomReserve from './assets/roomReserve.png'
import weatherDashboard from './assets/weatherDashboard.png'
import networkFileServer from './assets/networkFileServer.webp'
import task from './assets/task.png'
import metroid from './assets/metroid.png'
import Stack from '@mui/material/Stack'
import { Typography } from '@mui/material';
import Grid from '@mui/material/Grid2';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';
const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    ...theme.applyStyles('dark', {
      backgroundColor: '#1A2027',
    }),
  }));
export default function PersonalProjects() {
const theme = useTheme();

    const osText = (
        <div>
            <p>
            Collaborated with two teammates to build a robust network file server capable of handling multiple client requests concurrently. Implemented socket programming to handle client-server communication, efficient hand-over-hand locking to traverse the file system tree safely, and careful lock and unlock placement to avoid deadlock situations.
            </p>
            <p>
            <b>Code available on request </b>
            </p>
            
        </div>
        
        
    )
  return (
    <Box sx={{ flexGrow: 1 }} bgcolor={theme.palette.primary.dark} paddingTop={'200px'} paddingBottom={'20px'}>
          <span id="Projects"></span>
        <Container>
      
      
        <Typography variant='h3' color={theme.palette.primary.text} paddingBottom={2}>
            <b>PERSONAL PROJECTS</b>
            </Typography>
        <Grid container spacing={2}>
        <Card 
            image={metroid} 
            title='Metroid NES Unity Remaster'
            text="Remaster of the original 1986 Metroid NES version with Unity. Implemented player movement system, enemy AI behavior patterns, weapon switching and interactions, raycasting hit detection, and faithful level progression. Built for EECS 494 with teammate Andy He."
            githubLink={"http://3.148.17.119/"}
            />
            <Card 
            image={networkFileServer} 
            title='Operating Systems Project: C++ Concurrent network file server'
            text={osText}
           
            />
             <Card 
            image={weatherDashboard} 
            title='Simple Weather Dashboard'
            text='React-based application that utilizes the OpenWeather API to deliver comprehensive weather insights for cities globally. Key features include city-based lookup, real-time and "feels like" temperature, humidity, pressure, windspeed, visibility, and sunrise/sunset data.'
            githubLink='https://github.com/danielxt/weather-app'
        />
        <Card 
            image={chataway} 
            title='Chataway: LangChain AI trip planner'
            text='Worked with a team to develop a LangChain-powered chatbot that helps users plan their ideal trips by suggesting locations and providing a shortest-cycle route map. Utilized Google Places and Directions APIs to retrieve location data and applied traveling salesman heuristics in Python to optimize routes. Integrated Django to process chatbot responses and deliver data to the frontend seamlessly.'
            githubLink='https://github.com/danielxt/chatawaybackup'
        />

        <Card 
            image={roomReserve} 
            title='RoomReserve: Django room booking web application'
            text='Allows staff to book rooms for a given time and date and view room availability. Provides admins with additional privileges such as managing rooms, bookings and users.'
            githubLink='https://github.com/danielxt/DjangoRoomBookingProject'
        />
         <Card 
            image={task} 
            title='Assembly Soup Kitchen Website'
            text='Developed a website for a Klang Valley soup kitchen that provides 3000 meal kits monthly.'
            githubLink='https://theassemblysoupkitchen.com/'
        /> 

        
        </Grid>
  
        </Container>
    </Box>

    
  
  );
}